<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <!-- ************************ -->
      <div class="fixed-box">
      <!--      -->
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <!-- <nav-head :title="title" ></nav-head> -->
        <nav-head :title="languageStatus =='zh' ? '':'Address management'" ></nav-head>
      </div>
      </div>
      <!-- ************************ -->
      <!--  -->
      <div class="zhanwei"></div>
      <div class="item-box">
      <!-- list   -->
        <!-- {
      "id":"ID",
      "u_id":"ID",
      "currency":"",
      "address":"",
      "remarks":""
      }, -->
      <div class="item" @click.stop="listClick(item.address)" v-for="(item,index) in withdrawlistList" :key="index">
          <div class="item-right">
              <div class="row-1 row-g">
                  <img src="../../assets/image/capital/btb.png" alt="" style="vertical-align: middle;">
                  <span style="font-size: 0.28rem;margin-left: 0.24rem">{{item.remarks}}</span>
                  <span class="copy-text" @click.stop="copy(item.address)">{{$t('common.fuzhidizhi')}}</span>
              </div>
              <div class="row-2 row-g">{{item.address}}</div>
              <!-- <div class="row-3 row-g">2020.06.04 15.47</div> -->
          </div>
      </div>
      </div>
      <!--   btn -->
      <div class="del-btn" @click="get_jiebi_kk">
          {{$t('common.tianjiadizhi')}}
      </div>
      <!-- ********************************************************************************************* -->
                <div class="mask-bg" :class="mask_bg_isShow?'show-status':''"></div>
          <!--     -->
          <div class="main-box1" :class="mask_bg_isShow?'show-status1':''">
              <div class="al-title">
                  <span class="all-text">{{$t('common.tianjiadizhi')}}</span>
                  </div>
              <div class="pull-down">
                  <van-dropdown-menu>
                    <van-dropdown-item @change="ccc" v-model="value1" :options="option" />
                  </van-dropdown-menu>
              </div>
              <!-- <p class="item-g">
                  <span></span>
                  <span>0.00000000</span>
              </p> -->
              <div class="input-num">
                  <span class="num-a">
                    <input v-if="languageStatus=='en'" type="text" v-model="addwithdrawData.address" placeholder="enter the address">
                    <input v-else type="text" v-model="addwithdrawData.address" placeholder="">
                  </span>
              </div>
              <div class="input-num">
                  <span class="num-a">
                    <input v-if="languageStatus=='en'" type="text" v-model="addwithdrawData.remarks" placeholder="enter a description">
                    <input v-else type="text" v-model="addwithdrawData.remarks" placeholder="">
                  </span>
              </div>
              <div class="b-btn" @click="userSub()">{{$t('common.queren')}}</div>
              <div class="uuu">
                  <img src="../../assets/image/pic/close.png" alt="" class="close-img-b" @click="closeKK">
              </div>
          </div>
          <!-- ******************************************************************************************************* -->

    </div>
  </div>
</template>

<script>

import Vue from 'vue'
import NavHead from '@/components/head.vue'
import { withdrawlist, addwithdraw, coinList } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { Search, Tab, Tabs, Checkbox, CheckboxGroup, DropdownMenu, DropdownItem, Toast } from 'vant'
Vue.use(Checkbox).use(Search).use(CheckboxGroup).use(Tab).use(Tabs).use(DropdownMenu).use(DropdownItem).use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      languageStatus: '',
      title: '',
      curHeight: '',
      checked: true,
      active: 0,
      radio: '1',
      value: '',
      mask_bg_isShow: false,
      mask1_bg_isShow: false,
      kk2_isShow: false,
      value1: 0,
      switch1: false,
      switch2: false,
      coinList: {
        u_id: ''
      },
      withdrawlistData: {
        u_id: ''
      },
      addwithdrawData: {
        u_id: '', // id
        coin: '', // 
        address: '', // 
        remarks: '' // 
      }, // 
      withdrawlistList: [], // 
      coinData: {
        u_id: ''
      },
      option: [],
      userAdderss: ''
    }
  },
  created () {
    this.beforeMount()
    var adderss = this.$route.query.adderss
    this.userAdderss = adderss
    console.log('111111111', adderss)
    this.withdrawlistData.u_id = sessionStorage.getItem('u_id')
    this.coinData.u_id = sessionStorage.getItem('u_id')
    this.addwithdrawData.u_id = sessionStorage.getItem('u_id')
    this.coinFun() // 
    this.withdrawFun() // 
    this.getLanguageStatus()
  },
  methods: {
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    listClick (address) {
      if (this.userAdderss === 1) {
        console.log('211111111')
        sessionStorage.setItem('userAdderss', address)
        // history.go(-1)
        history.back()
      }
      console.log('11111', this.userAdderss, address)
    },
    copy (data) {
      var url = data
      var oInput = document.createElement('input')
      oInput.value = url
      document.body.appendChild(oInput)
      oInput.select() // ;
      document.execCommand('Copy') // 
      this.$toast({ message: '', duration: 1500 })
    },
    userSub () {
      if (this.addwithdrawData.address === '') {
        this.$toast({ message: '', duration: 1500 })
        return
      }
      if (this.addwithdrawData.remarks === '') {
        this.$toast({ message: '', duration: 1500 })
        return
      }
      this.addwithdrawFun()
    },
    ccc (value) { // 
      this.addwithdrawData.coin = this.option[value].text
    },
    // 
    withdrawFun () {
      var that = this
      var data = objKeySort(this.withdrawlistData)
      var token = sgin(data)
      this.withdrawlistData.token = token
      withdrawlist(that.withdrawlistData).then(res => {
        that.withdrawlistList = res.data
        delete that.withdrawlistData.token
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        delete that.withdrawlistData.token
      })
    },
    coinFun () {
      var that = this
      var data = objKeySort(this.coinData)
      var token = sgin(data)
      this.coinData.token = token
      coinList(this.coinData).then(res => {
        that.list = res.data
        for (const [i, v] of res.data.entries()) {
          var arr = {
            text: '',
            value: 0
          }
          arr.text = v.type
          arr.value = i
          that.option.push(arr)
        }
        this.addwithdrawData.coin = this.option[0].text
      }).catch((err) => console.log(err))
    },
    addwithdrawFun () {
      var that = this
      var data = objKeySort(this.addwithdrawData)
      var token = sgin(data)
      this.addwithdrawData.token = token
      addwithdraw(this.addwithdrawData).then(res => {
        if (res.code === 0) {
          that.$toast({
            message: '',
            duration: 1500
          })
          that.withdrawFun()
          that.addwithdrawData.address = ''
          that.addwithdrawData.remarks = ''
          that.mask_bg_isShow = false
          delete that.addwithdrawData.token
        }
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        that.addwithdrawData.address = ''
        that.addwithdrawData.remarks = ''
        that.mask_bg_isShow = false
        delete that.addwithdrawData.token
      })
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    //    
    get_jiebi_kk () {
      this.mask_bg_isShow = !this.mask_bg_isShow
    },
    //  
    closeKK () {
      this.mask_bg_isShow = !this.mask_bg_isShow
    }
  }
}
</script>
